
<div class="field">
  <div class="py-3">
    <treeselect
      ref="robotsselect"
      v-model="componentValue"
      :multiple="true"
      :flat="true"
      :disable-branch-nodes="true"
      :default-expand-level="0"
      :options="options"
      :disabled="disabled"
    >
      <div
        slot="value-label"
        slot-scope="{ node }"
      >
        {{ node.raw.customLabel }}
      </div>
    </treeselect>
  </div>
</div>
