
<div class="field">
  <div class="py-3">
    <treeselect
      ref="treeselect"
      v-model="value"
      :multiple="false"
      :flat="false"
      :default-expand-level="0"
      :options="options"
      :disabled="disabled"
    />
  </div>
  <div
    v-if="schemaName !== null"
    class="heading"
  >
    <div class="instructions">
      <p>
        <a
          :href="'http://schema.org/' + schemaName"
          rel="noopener"
          target="_blank"
        >{{ schemaName }} info: </a>
        {{ schemaDescription }}
      </p>
    </div>
  </div>
</div>
